import { Injectable } from '@angular/core';

import { Api } from 'core/services/api.service';

export class EmployeeBasic {

  user_id: string;
  name: string;
}

export class ApplyWfh {

  date: string = '';
  summary: string = '';
  notify_users: Array<EmployeeBasic>;
}

export class Wfh {

  _id: string;
  user_id: string;
  name: string;
  date: string;
  summary: string;
  reviewed_by: EmployeeBasic;
  wfh_status: number;
}

@Injectable()
export class WfhService extends Api {

  private _base_url = '/wfhs';

  apply(wfh: ApplyWfh): Promise<Object> {

    return this.post(this._base_url, wfh)
      .then(response => response);
  }

  getWfhs(query?: Map<string, any>): Promise<Wfh[]> {

    return this.get(this._base_url, query)
      .then(response => response['data'] as Wfh[]);
  }

  getReviewWfhs(query?: Map<string, any>): Promise<Wfh[]> {

    return this.get(this._base_url + '?review=true', query)
      .then(response => response['data'] as Wfh[]);
  }

  approve(wfh_id: string): Promise<Object> {

    return this.patch(this._base_url + '/review/' + wfh_id, {})
      .then(response => response);
  }

  reject(wfh_id: string, reject_reason: string): Promise<Object> {

    return this.delete(this._base_url + '/review/' + wfh_id, {reason: reject_reason})
      .then(response => response);
  }

  cancel(wfh_id: string): Promise<Object> {

    return this.delete(this._base_url + '/' + wfh_id)
      .then(response => response);
  }
}
