import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';

import { Wfh, WfhService } from 'modules/work-from-home/work-from-home.service';
import { STATUS } from 'config/constants/wfh';
import { Access } from 'core/providers/access.service';
import { ExaTask } from '../../../core/providers/exatask.service';
import { ProfileService } from '../../user/profile/profile.service';

@Component({
  selector: 'app-list-wfh',
  templateUrl: './list-wfh.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [WfhService, ProfileService]
})
export class ListWfhComponent implements OnInit {

  wfhs: Wfh[] = [];

  applyAllowed: boolean = false;
  reviewAllowed: boolean = false;
  wfhPolicyAllowed: boolean = false;
  wfhPolicyExists: boolean = false;

  loading: boolean = false;
  cancelling: Array<Boolean>;

  constructor(
    private wfhService: WfhService,
    private profileService: ProfileService,
    private access: Access,
    public exatask: ExaTask
  ) {

    this.cancelling = new Array<Boolean>();
    this.exatask.setTitle('WFH List');
  }

  async ngOnInit() {

    this.applyAllowed = await this.access.hasAccess({
      module: 'work-from-home',
      entity: 'work-from-home',
      action: 'create'
    });

    this.reviewAllowed = await this.access.hasAccess({
      module: 'work-from-home',
      entity: 'work-from-home',
      action: 'review'
    });

    this.wfhPolicyAllowed = await this.access.hasAccess({
      module: 'work-from-home',
      entity: 'policy',
      action: 'create'
    });

    this.profileService.getProfile('policies').then(profile => {

      if (profile.policies && profile.policies.wfh && profile.policies.wfh.length > 0) {
        this.wfhPolicyExists = true;
      } else {
        this.applyAllowed = false;
      }
    });

    this.loadWfhs();
  }

  loadWfhs() {

    const self = this;
    this.loading = true;
    this.wfhService.getWfhs().then(wfhs => {

      this.loading = false;
      this.wfhs = wfhs;
    });
  }

  cancelWfh(index) {

    const wfh_id = this.wfhs[index]._id;
    const self = this;

    this.cancelling[index] = true;
    this.wfhService.cancel(wfh_id).then(response => {

      self.wfhs[index].wfh_status = STATUS.DELETED;
      self.cancelling[index] = false;
    });
  }

  getStatusBadge(status) {

    switch (status) {

      case 2: return 'badge-success';
      case 3: return 'badge-danger';
      case 4: return 'badge-dark';
      default: return 'badge-default';
    }
  }
}
