import { Component, ViewEncapsulation, OnInit, ViewChild } from '@angular/core';
import { Wfh, WfhService } from 'modules/work-from-home/work-from-home.service';
import { STATUS } from 'config/constants/wfh';
import { Access } from 'core/providers/access.service';
import { ExaTask } from '../../../core/providers/exatask.service';
import { find, matchesProperty } from 'lodash';
import { ModalDirective } from 'ngx-bootstrap';

enum Operation {
  APPROVE = 1, REJECT
}

@Component({
  selector: 'app-review-wfh',
  templateUrl: './review-wfh.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [WfhService]
})
export class ReviewWfhComponent implements OnInit {
  @ViewChild('confirmModal') confirmModal: ModalDirective;

  wfhs: Wfh[] = [];
  wfh_id: string;
  wfh: Wfh;

  reviewed: Array<Boolean>;
  approving: Array<Boolean>;
  rejecting: Array<Boolean>;
  loading: boolean = false;

  approveAllowed: boolean;
  rejectAllowed: boolean;

  operation: Operation = Operation.APPROVE;
  reject_reason: string;

  constructor(
    private wfhService: WfhService,
    private access: Access,
    public exatask: ExaTask
  ) {

    this.reviewed = [];
    this.approving = [];
    this.rejecting = [];
    this.exatask.setTitle('WFH Review List');
  }

  async ngOnInit() {

    this.loadWfhs();

    this.approveAllowed = await this.access.hasAccess({
      module: 'work-from-home',
      entity: 'review',
      action: 'approve'
    });

    this.rejectAllowed = await this.access.hasAccess({
      module: 'work-from-home',
      entity: 'review',
      action: 'reject'
    });
  }

  private loadWfhs() {

    const self = this;
    const query = new Map<string, any>();
    query.set('wfh_status', STATUS.APPLIED);

    this.loading = true;

    this.wfhService.getReviewWfhs(query).then(wfhs => {

      self.loading = false;
      self.wfhs = wfhs;
    });
  }

  approve(wfh_id) {

    this.approving[wfh_id] = true;
    this.wfhService.approve(wfh_id).then(response => {

      this.reviewed[wfh_id] = true;
      this.approving[wfh_id] = false;
    });
  }

  reject(wfh_id) {

    this.rejecting[wfh_id] = true;
    this.wfhService.reject(wfh_id, this.reject_reason).then(response => {

      this.reviewed[wfh_id] = true;
      this.rejecting[wfh_id] = false;
      this.reject_reason = '';
    });
  }

  wfhConfirmation(operation, wfh_id) {

    this.operation = operation;
    this.wfh_id = wfh_id;
    this.wfh = find(this.wfhs, matchesProperty('_id', wfh_id));
    this.reject_reason = '';
    this.confirmModal.show();
  }
}
